import React from 'react';
import './Notes.css';
import { BrowserRouter as Router, Route, Link} from "react-router-dom";

import Prism from 'prismjs';
import 'prismjs/components/prism-java.min.js'
import 'prismjs/components/prism-python.min.js'

var iframe;

class App extends React.Component {
    render() {
        return (
            <Router>
                <Route path="/notes/*" component={
                    ({match}) => {
                        if (match.url === '/notes/') {
                            return <Menu/>;
                        } else {
                            return <Editor file={match.url.replace('/notes/', '')}/>;
                        }
                    }
                }/>
          </Router>
        );
    }
}

class Menu extends React.Component {
    componentDidMount() {
        window.addEventListener('pageshow', (event) => {
            document.getElementById('files').innerHTML = '';
            let files = {};

            caches.open('files').then((cache) => {
                cache.keys().then(keys => {
                    keys.forEach((key) => {
                        let matches = key.url.replace('https://18nshinagawa.github.io/notes/', '').split('/');
                        let file = files;
                        matches.forEach((match, index) => {
                            if (!file[match]) {
                                if (index === matches.length - 1) {
                                    try {
                                        file[match] = key.url.replace('https://18nshinagawa.github.io/notes/', '');
                                    } catch(error) {
                                        console.error(error);
                                    }
                                } else {
                                    file[match] = {};
                                }
                            }
                            file = file[match];
                        })
                    });

                    function menu(files, parent) {
                        if (typeof files === 'object') {
                            Object.keys(files).forEach((file) => {
                                let div = document.createElement('div');
                                div.innerText = file;
                                parent.append(div);
                                menu(files[file], div);
                            });
                        } else {
                            parent.innerHTML = '<a href="' + files + '">' + files.replace(/.*\//, '') + '</a>';
                        }
                    }

                    menu(files, document.getElementById('files'));
                });
            });
        });
    }

    render () {
        return (
            <div id="container">
                <div id="files"></div>
            </div>
        );
    }
}

class Editor extends React.Component {
    element = '';
    placeholders = {math: ['math'], code: ['code', 'language'], image: ['url', 'text'], link: ['url', 'text']};
    display = false;
    languages = ['python', 'java', 'html', 'css', 'javascript'];
    undo = 0;
    keyboard = false;
    keys = {'Space': ' ', 'Backspace': 'delete', 'Enter': 'return', 'Tab': 'tab', ' ': ' ', 'CapsLock': 'caps lock', 'ArrowRight': 'right', 'ArrowLeft': 'left', 'ArrowUp': 'up', 'ArrowDown': 'down', 'Backquote': '`', 'Minus': '-', 'Equal': '=', 'BracketLeft': '[', 'BracketRight': ']', 'Backslash': '\\', 'Semicolon': ';', 'Quote': '\'', 'Comma': ',', 'Period': '.', 'Slash': '/', 'ShiftLeft': 'shift-left', 'ShiftRight': 'shift-right', 'ControlLeft': 'control-left', 'ControlRight': 'control-right', 'AltLeft': 'option-left', 'AltRight': 'option-right', 'OSLeft': 'command-left', 'OSRight': 'command-right'};
    style = `
    <link rel="stylesheet" href="https://cdn.jsdelivr.net/npm/katex@0.10.2/dist/katex.min.css" integrity="sha384-yFRtMMDnQtDRO8rLpMIKrtPCD5jdktao2TV19YiZYWMDkUR5GQZR/NOVTdquEx1j" crossorigin="anonymous">
    <style>

    body {
        margin: 0;
        background-color: #333333;
        overflow-x: hidden;
    }

    #editor {
        width: 21cm;
        padding: 2.5cm;
        background-color: white;
        color: #333333;
        outline: none;
        font-family: 'KaTeX_Main', serif;
        line-height: 1.5;
        word-break: break-word;
        caret-color: #4488FF;
        margin: 1.25cm auto;
        box-sizing: border-box;
    }

    #editor.saved {
        border-left: 2px solid #00BB66;
    }

    #editor {
        border-left: 2px solid #FF4444;
    }
    
    ::-moz-selection {
        background-color: rgba(68, 136, 255, 0.25);
    }
    
    #editor > :first-child {
        margin-top: 0;
    }
    
    #editor > :last-child {
        margin-bottom: 0;
    }

    h1, h2, h3 {
        font-weight: normal;
        text-align: center;
    }
    
    img {
        width: 10.5cm;
        display: block;
        margin: auto;
    }

    :-moz-broken, :-moz-loading {
        height: 7.4cm;
        border: 2px solid red;
        box-sizing: border-box;
        text-align: center;
        line-height: 7.4cm;
        color: #FF4444;
    }

    :-moz-broken::after {
        content: '404 (not found)';
    }

    .caption {
        text-align: center;
        margin-top: 0.5em;
    }

    .object {
        -moz-user-select: all;
    }

    .object[data-object="math"] {
        max-width: 16cm;
        overflow: hidden;
        display: inline-flex;
    }
    
    .object[data-object="math"].display {
        display: block;
    }

    a {
        color: #4488FF;
    }
    
    blockquote {
        margin: 1em 0;
        padding: 1em;
        border-left: 2px solid #333333;
    }

    blockquote > :first-child {
        margin-top: 0;
    }
    
    blockquote > :last-child {
        margin-bottom: 0;
    }
    
    ul, ol {
        color: inherit;
    }

    [data-object="code"] {
        background-color: #EEEEEE;
        font-family: 'KaTeX_Typewriter', monospace;
        max-width: 16cm;
        overflow: hidden;
        display: inline;
        color: #333333;
    }

    [data-object="code"].display {
        white-space: pre-wrap;
        display: block;
        padding: 8px;
        margin: 1em 0;
    }

    .token.string, .token.attr-value {
        color: #FF8844;
    }

    .token.keyword, .token.boolean, .token.attr-name, .token.selector {
        color: #AA44FF;
    }

    .token.function, .token.tag, .token.rule, .token.builtin {
        color: #4488FF;
    }

    .token.class-name, .token.number, .token.doctype, .token.property {
        color: #00BB66;
    }

    .token.punctuation {
        color: #888888;
    }

    .token.regex, .token.parameter, .token.url {
        color: #FF4444;
    }

    .token.comment {
        color: #888888;
    }

    .token.operator {
        color: #888888;
    }

    table {
        border-collapse: collapse;
        margin: auto;
    }

    td, th {
        border: 2px solid black;
        padding: 8px;
    }
    
    .page-break {
        display: block;
        height: 0.3125cm;
        width: calc(100% + 5cm);
        background-color: #333333;
        left: -2.5cm;
        position: relative;
        margin: 2.5cm 0;
        -moz-user-select: none;
    }
    
    @media print {
        #editor {
            margin: 0;
            padding: 0;
            color-adjust: exact;
        }

        .page-break {
            page-break-after: always;
            visibility: hidden;
        }
    }

    @page {
        margin: 2.5cm;
    }

    </style>
    `

    componentDidMount() {
        iframe = document.getElementById('iframe').contentWindow.document;

        caches.open('files').then((cache) => {
            cache.match('/notes/' + this.props.file).then((response) => 
                response.text()
            ).then((text) => {
                iframe.getElementById('editor').innerHTML = text;
                this.handleInputIframe();
                iframe.getElementById('editor').classList.add('saved');
            }).catch((error) => {
                this.handleInputIframe();
                iframe.getElementById('editor').classList.add('saved');
            })
        });

        iframe.open();
        iframe.write('<!DOCTYPE html>' + this.style + '<div id="editor" spellcheck="false" contentEditable="true"></div>');
        iframe.close();

        iframe.execCommand("defaultParagraphSeparator", false, 'p');

        iframe.getElementById('editor').addEventListener('click', (event) => {
            this.handleClickIframe(event);
        });
        
        document.addEventListener('keydown', (event) => {
            let key = event.key.toLowerCase();
            if ((navigator.platform === 'MacIntel' && event.metaKey) || (navigator.platform !== 'MacIntel' && event.ctrlKey)) {
                if ('sop'.includes(key)) {
                    this.handleKeyDownIframe(event);
                } else if (event.key.toLowerCase() === 'k') {
                    event.preventDefault();
                    this.keyboard = !this.keyboard;
                    document.getElementById('keyboard').classList.toggle('keyboard');
                } else {
                    this.handleKeyDown(event);
                }
            } else {
                this.handleKeyDown(event);
            }
        });

        document.addEventListener('keyup', (event) => {
            this.handleKeyUp(event);
        });

        window.addEventListener('blur', (event) => {
            document.getElementById('keyboard').classList.remove('command');
            document.getElementById('keyboard').classList.remove('shift')
            Array.from(document.querySelectorAll('.down')).forEach((element) => {
                element.classList.remove('down');
            });
        });

        iframe.getElementById('editor').addEventListener('copy', (event) => {
            event.preventDefault();
            let selection = document.getElementById('iframe').contentWindow.getSelection();
            let range = selection.getRangeAt(0);
            let content = range.cloneContents();
            var div = document.createElement('div');
            div.id = 'clipboard-data';
            div.appendChild(content);
            event.clipboardData.setData("text/plain", div.outerHTML);
        });

        document.addEventListener('paste', (event) => {
            event.preventDefault();
            document.execCommand('insertHTML', false, event.clipboardData.getData("text/plain"));
        });

        iframe.getElementById('editor').addEventListener('paste', (event) => {
            event.preventDefault();
            if (event.clipboardData.getData('text/plain').match(/<div id="clipboard-data">(.*)<\/div>/)) {
                iframe.execCommand('insertHTML', false, event.clipboardData.getData('text/plain').match(/<div id="clipboard-data">(.*)<\/div>/)[1]);
            } else {
                iframe.execCommand('insertText', false, event.clipboardData.getData('text/plain'));
            }
        });
        
        iframe.getElementById('editor').addEventListener('keydown', (event) => {
            this.handleKeyDownIframe(event);
        });

        iframe.getElementById('editor').addEventListener('keyup', (event) => {
            if (this.keyboard) {
                this.handleKeyUp(event);
            }
        });

        iframe.getElementById('editor').addEventListener('input', (event) => {
            this.handleInputIframe(event);
        });

        window.addEventListener('beforeunload', (event) => {
            if (!iframe.getElementById('editor').classList.contains('saved')) {
                event.preventDefault();
            }
        });
    }

    handleKeyDownIframe(event) {
        let selection = document.getElementById('iframe').contentWindow.getSelection();
        let range;

        if (selection.rangeCount) {
            range = selection.getRangeAt(0);
            if (range.commonAncestorContainer.nodeType === 3 && range.commonAncestorContainer.parentElement.id === 'editor') {
                iframe.execCommand('formatBlock', false, 'p');
            }
        }

        let key = event.key.toLowerCase();

        if (this.keyboard) {
            event.preventDefault();
            this.handleKeyDown(event);
        }

        if ((navigator.platform === 'MacIntel' && event.metaKey) || (navigator.platform !== 'MacIntel' && event.ctrlKey)) {
            if (key === 'k') {
                event.preventDefault();
                this.keyboard = !this.keyboard;
                document.getElementById('keyboard').classList.toggle('keyboard');
            } else if ('123'.indexOf(key) !== -1) {
                event.preventDefault();
                iframe.execCommand('heading', false, 'h' + key);
            } else if (key === '0') {
                event.preventDefault();
                iframe.execCommand('formatBlock', false, 'p');
            } else if (key === 'l') {
                event.preventDefault();
                iframe.execCommand(event.shiftKey ? 'insertOrderedList' : 'insertUnorderedList');
            } else if (key === 'b') {
                event.preventDefault();
                
                if (!event.shiftKey) {
                    iframe.execCommand('bold');
                }
            } else if (key === 'i') {
                event.preventDefault();

                if (!event.shiftKey) {
                    iframe.execCommand('italic');
                }
            } else if ('jghm'.includes(key)) {
                event.preventDefault();

                if (event.shiftKey) {
                    this.display = true;
                    document.getElementById('display').checked = true;
                } else {
                    this.display = false;
                    document.getElementById('display').checked = false;
                }

                this.element = {j: 'code', m: 'math', h: 'link', g: 'image'}[key];
                this.undo = 1;
                iframe.execCommand('insertHTML', false, '<span class="selection ' + (this.display ? 'display': '') + '"></span>');
                document.getElementById('first').dataset.placeholder = this.placeholders[this.element][0];
                document.getElementById('second').dataset.placeholder = this.placeholders[this.element][1];
                document.getElementById('edit').style.top = iframe.querySelector('.selection').getBoundingClientRect().top + iframe.body.scrollTop + 'px';
                document.getElementById('edit').classList.add('focus');
                document.getElementById('first').innerHTML = '<br>';
                document.getElementById('second').innerHTML = '<br>';
                document.getElementById('first').focus();
                
                if (this.element === 'code') {
                    document.getElementById('second').classList.add('error');
                } else {
                    document.getElementById('second').classList.remove('error');   
                }
            } else if (event === 'p') {
                event.preventDefault();
                document.getElementById('iframe').contentWindow.print();
            } else if (key === 's') {
                event.preventDefault();

                if (event.shiftKey) {
                    let blob = new Blob([this.style + '<div id="editor" class="saved">' + iframe.getElementById('editor').innerHTML + '</div>'], {type: 'text/html'});
                    let a = window.document.createElement('a');
                    a.href = window.URL.createObjectURL(blob);
                    a.download = (document.querySelector('title').innerText || 'untitled') + '.html';
                    document.body.appendChild(a);
                    a.click();
                    document.body.removeChild(a);
                }

                iframe.getElementById('editor').classList.add('saved');

                caches.open('files').then((cache) => {
                    cache.put('/notes/' + this.props.file, new Response(iframe.getElementById('editor').innerHTML));
                });
            } else if (key === 'o') {
                event.preventDefault();
                document.getElementById('open').click();
            } else if (event.key === 'Enter') {
                event.preventDefault();
                let selection = document.getElementById('iframe').contentWindow.getSelection();
                let range = selection.getRangeAt(0);
                let p = false;
                
                let end = false;

                if (range.commonAncestorContainer.nodeType === 3 && range.commonAncestorContainer.parentElement.id === 'editor') {
                    iframe.execCommand('formatBlock', false, 'p');
                }

                if (range.commonAncestorContainer.nodeType !== 3 && (!range.nextSibling || (range.nextSibling.classList && range.nextSibling.classList.contains('page-break')))) {
                    p = true;
                }

                if (range.commonAncestorContainer.nodeType !== 3 && range.Offset === range.commonAncestorContainer.childNodes.length) {
                    p = true;
                }

                if ((!range.commonAncestorContainer.nextSibling || range.commonAncestorContainer.nextSibling.nodeName === 'BR') && range.commonAncestorContainer.nodeValue && range.commonAncestorContainer.nodeValue.length === range.endOffset) {
                    end = true;
                    if (!range.commonAncestorContainer.parentElement.nextSibling || (range.commonAncestorContainer.parentElement.nextSibling.classList && range.commonAncestorContainer.parentElement.nextSibling.classList.contains('page-break'))) {
                        p = true;
                    }
                }

                if (range.startOffset === 0 && range.startContainer.parentElement.firstChild === range.startContainer) {
                    if (!range.startContainer.parentElement.previousSibling || (range.startContainer.parentElement.previousSibling.classList && range.startContainer.parentElement.previousSibling.classList.contains('page-break'))) {
                        return;
                    }
                }

                if (range.startContainer.nodeType !== 3 && range.startOffset === 0) {
                    if (!range.startContainer.previousSibling) {
                        return;
                    }
                }

                if (range.startOffset === 0 && range.commonAncestorContainer.parentElement === iframe.getElementById('editor').firstChild) {
                    return;
                }

                if (range.commonAncestorContainer.parentElement.id === 'editor') {
                    if (range.commonAncestorContainer.previousSibling && range.commonAncestorContainer.previousSibling.classList.contains('page-break')) {
                        if (!range.commonAncestorContainer.previousSibling || range.commonAncestorContainer.previousSibling.classList.contains('page-break')) {
                            return;
                        }
                    }
                }
                
                if (p) {
                    iframe.execCommand('insertHTML', false, '<div class="page-break"><br></div><p><br></p>');
                } else {
                    iframe.execCommand('insertHTML', false, '<div class="page-break"><br></div>');
                    if (!end) {
                        let newRange = iframe.createRange();
                        if (range.commonAncestorContainer.nodeType === 3) {
                            newRange.setStart(range.commonAncestorContainer.parentElement, 0);
                            newRange.setEnd(range.commonAncestorContainer.parentElement, 0);
                        } else {
                            newRange.setStart(range.commonAncestorContainer, 0);
                            newRange.setEnd(range.commonAncestorContainer, 0);
                        }
                        selection.removeAllRanges();
                        selection.addRange(newRange);
                    } else {
                        let newRange = iframe.createRange();
                        newRange.setStart(range.commonAncestorContainer.parentElement.nextSibling.nextSibling, 0);
                        newRange.setEnd(range.commonAncestorContainer.parentElement.nextSibling.nextSibling, 0);
                        selection.removeAllRanges();
                        selection.addRange(newRange);
                    }
                }
            } else if (event.key === 'Backspace') {
                let selection = document.getElementById('iframe').contentWindow.getSelection();
                let range = selection.getRangeAt(0);
                if (range.startOffset === 0 && range.startContainer.parentElement.previousSibling && range.startContainer.parentElement.previousSibling.classList.contains('page-break')) {
                    event.preventDefault();
                } else if (range.commonAncestorContainer.previousSibling && range.commonAncestorContainer.previousSibling.classList.contains('page-break')) {
                    event.preventDefault();
                }
            } else if ('afxcrd'.includes(key) && event.shiftKey) {
                event.preventDefault();
            } else if ('ru'.includes(key)) {
                event.preventDefault();
            }
        } else if (event.key === 'Tab') {
            event.preventDefault(); 
            if (event.shiftKey) {
                iframe.execCommand('outdent');
            } else {
                iframe.execCommand('indent');
            }
        } else if (event.key === 'Backspace') {
            let selection = document.getElementById('iframe').contentWindow.getSelection();
            let range = selection.getRangeAt(0);
            
            if (range.startContainer === range.endContainer && range.startOffset === range.endOffset) {
                if (range.startContainer.previousSibling && range.startOffset === 0 && (range.startContainer.previousSibling.classList.contains('object'))) {
                    event.preventDefault();
                    selection.removeAllRanges();
                    range.selectNode(range.startContainer.previousSibling);
                    selection.addRange(range);
                } else if (range.startContainer.children && range.startContainer.children[range.startOffset - 1] && range.startContainer.children[range.startOffset - 1].classList.contains('object')) {
                        event.preventDefault();
                        selection.removeAllRanges();
                        range.selectNode(range.startContainer.children[range.startOffset - 1]);
                        selection.addRange(range);
                } else if (range.startContainer.children && range.startContainer.children[range.startOffset - 2] && range.startContainer.children[range.startOffset - 2].classList.contains('object')) {
                    event.preventDefault();
                    selection.removeAllRanges();
                    range.selectNode(range.startContainer.children[range.startOffset - 2]);
                    selection.addRange(range);
                } else if (range.startOffset === 0 && range.startContainer.parentElement.previousSibling && range.startContainer.parentElement.previousSibling.classList.contains('page-break')) {
                    event.preventDefault();
                    let newRange = iframe.createRange();
                    newRange.setStartAfter(range.startContainer.parentElement.previousSibling.previousSibling.firstChild);
                    newRange.setEndBefore(range.startContainer.parentElement);
                    selection.removeAllRanges();
                    selection.addRange(newRange);
                    iframe.execCommand('delete');
                    let newerRange = iframe.createRange();
                    newerRange.setStart(newRange.startContainer.nextSibling, 0);
                    newerRange.setEnd(newRange.startContainer.nextSibling, 0);
                    selection.removeAllRanges();
                    selection.addRange(newerRange);
                } else if (range.commonAncestorContainer.previousSibling && range.commonAncestorContainer.previousSibling.classList.contains('page-break')) {
                    event.preventDefault();
                    let newRange = iframe.createRange();
                    newRange.setStartAfter(range.startContainer.previousSibling.previousSibling.firstChild);
                    newRange.setEndBefore(range.startContainer);
                    selection.removeAllRanges();
                    selection.addRange(newRange);
                    iframe.execCommand('delete');
                    let newerRange = iframe.createRange();
                    newerRange.setStart(newRange.startContainer.nextSibling, 0);
                    newerRange.setEnd(newRange.startContainer.nextSibling, 0);
                    selection.removeAllRanges();
                    selection.addRange(newerRange);
                }
            }
        }
    }

    handleClickIframe(event) {
        let object;

        if (event) {
            object = event.target.closest('.object');
      
            if (iframe.querySelector('.selection')) {
                iframe.querySelector('.selection').classList.remove('selection');
            }
        } else {
            object = iframe.querySelector('.selection');
        }

        if (object) {
            let selection = document.getElementById('iframe').contentWindow.getSelection();
            selection.removeAllRanges();
            object.classList.add('selection');
            document.getElementById('first').innerHTML = object.dataset.first.replace(/\n$/g, '').replace(/</g, '&lt;').replace(/>/g, '&gt;').replace(/"/g, '&quot;') || '<br>';
            document.getElementById('second').innerHTML = object.dataset.second.replace(/\n$/g, '').replace(/</g, '&lt;').replace(/>/g, '&gt;').replace(/"/g, '&quot;') || '<br>';
            this.undo = 0;
           
            if (event.target.closest('.object').dataset.object === 'code' && !this.languages.includes(document.getElementById('second').innerText)) {
                document.getElementById('second').classList.add('error');
            } else {
                document.getElementById('second').classList.remove('error');
            }
           
            document.getElementById('edit').classList.add('focus');
            document.getElementById('edit').style.top = object.getBoundingClientRect().top + iframe.body.scrollTop + 'px';
            this.element = object.dataset.object
          
            if (object.classList.contains('display')) {
                this.display = true;
                document.getElementById('display').checked = true;
            } else {
                this.display = false;
                document.getElementById('display').checked = false;
            }
            document.getElementById('first').dataset.placeholder = this.placeholders[this.element][0];
            document.getElementById('second').dataset.placeholder = this.placeholders[this.element][1];
            selection = window.getSelection();
            let range = document.createRange();
            range.setStartAfter(document.getElementById('first').firstChild);
            selection.removeAllRanges();
            selection.addRange(range);
        } else {
            document.getElementById('edit').classList.remove('focus');
        }
    }

    handleInputIframe(event) {
        let selection = document.getElementById('iframe').contentWindow.getSelection();
        let range = selection.getRangeAt(0);

        document.querySelector('title').innerText = this.props.file.replace(/.*\//, '');

        Array.from(iframe.getElementsByTagName('h1')).forEach((element, index) => {
            element.id = element.innerText.replace(/\s/g, '');
            if (index === 0) {
                document.querySelector('title').innerText = element.innerText;
            }
        });
        
        Array.from(iframe.querySelectorAll('h2, h3')).forEach((element) => {
            element.id = element.innerText.replace(/\s/g, '');
        });

        if (range.startContainer.id !== 'editor' && range.endContainer.id === 'editor') {
            let newRange = iframe.createRange();
            newRange.setStart(range.startContainer.nextSibling.nextSibling, 0);
            selection.removeAllRanges();
            selection.addRange(newRange);
        }

        document.getElementById('iframe').style.height = iframe.getElementById('editor').clientHeight + 96 + 'px';
        iframe.getElementById('editor').classList.remove('saved');
    }
    
    handleInput(event) {
        if (iframe.querySelector('.selection')) {
            let range = iframe.createRange();
            let selection = document.getElementById('iframe').contentWindow.getSelection();
            range.selectNode(iframe.querySelector('.selection'));
            selection.removeAllRanges();
            selection.addRange(range);
        }

        iframe.getElementById('editor').focus();
        let first = document.getElementById('first').innerText.replace(/\n$/g, '').replace(/</g, '&lt;').replace(/>/g, '&gt;').replace(/"/g, '&quot;');
        let second = document.getElementById('second').innerText.replace(/\n$/g, '').replace(/</g, '&lt;').replace(/>/g, '&gt;').replace(/"/g, '&quot;');
        let scroll = document.documentElement.scrollTop;

        if (this.undo > 0) {
            iframe.execCommand('undo');
        }

        if (this.element === 'image') {
            if (first.match(/\S+/g)) {
                iframe.execCommand('insertHTML', false, '<div class="selection object" data-object="image" data-first="' + first + '" data-second="' + second + '"><div class="image"><img src="' + first + '" alt=" "/></div><div class="caption">' + second + '</div></div>');
                this.undo ++;
                document.getElementById('iframe').style.height = iframe.getElementById('editor').clientHeight + 376 + 'px';

                iframe.querySelector('.selection img').onload = () => {
                    document.getElementById('iframe').style.height = iframe.getElementById('editor').clientHeight + 96 + 'px';
                };
            } else {
                iframe.execCommand('delete');
            }
        } else if (this.element === 'link') {
            if (second.match(/\S+/g)) {
                iframe.execCommand('insertHTML', false, '<span class="selection object" data-object="link" data-first="' + first + '" data-second="' + second + '"><a href="' + first + '">' + second + '</a></span>');
                this.undo ++;
            } else {
                iframe.execCommand('delete');
            }
        } else if (this.element === 'math') {
            if (first.match(/\S+/g)) {
                iframe.execCommand('insertHTML', false, '<span class="selection object ' + (this.display ? 'display' : '') + '" data-object="math" data-first="' + first + '">' + window.katex.renderToString(first, {displayMode: this.display, throwOnError: false, errorColor: '#DD4433'}) + '</span>');
                this.undo ++;
            } else {
                iframe.execCommand('delete');
            }
        } else if (this.element === 'code') {
            if (first.match(/\S+/g)) {
                if (this.languages.includes(second)) {
                    document.getElementById('second').classList.remove('error');
                    iframe.execCommand('insertHTML', false, '<span class="selection object ' + (this.display ? 'display' : '') + '" data-object="code" data-first="' + first + '" data-second="' + second + '">' + Prism.highlight(first, Prism.languages[second], second) + '</span>');
                    this.undo ++;
                } else {
                    document.getElementById('second').classList.add('error');
                    iframe.execCommand('insertHTML', false, '<span class="selection object ' + (this.display ? 'display' : '') + '" data-object="code" data-first="' + first + '" data-second="' + second + '">' + first + '</span>');
                    this.undo ++;
                }
            } else {
                iframe.execCommand('delete');
            }
        }

        if (iframe.querySelector('.selection')) {
            document.getElementById('edit').style.top = iframe.querySelector('.selection').getBoundingClientRect().top + iframe.body.scrollTop + 'px';
        }
        
        if (event && event.target) {
            event.target.focus();
        }
        // issue with preventing scroll
        //window.setTimeout(function() {window.scrollTo(0, 0);}, 1000);
    }

    handleKeyDown(event) {
        if (this.keyboard) {
            event.preventDefault();
            if (event.code === 'Escape') {
                this.keyboard = false;
                document.getElementById('keyboard').classList.remove('keyboard');
                return;
            } else  if (event.code.match('Digit')) {
                document.getElementById(event.code.replace('Digit', '')).classList.add('down');
            } else if (event.code.match('Key')) {
                document.querySelector('.row:not(:first-child) #' + event.code.replace('Key', '').toLowerCase()).classList.add('down');
            } else if (Object.keys(this.keys).includes(event.code)) {
                document.getElementById(this.keys[event.code]).classList.add('down');
            }

            if (event.shiftKey) {
                document.getElementById('keyboard').classList.add('shift');
            }
            
            if (((navigator.platform === 'MacIntel' && event.metaKey) || (navigator.platform !== 'MacIntel' && event.ctrlKey))) {
                document.getElementById('keyboard').classList.add('command');
            }
        } else {
            if (event.key === ' ') {
                if (document.querySelector(':focus') && document.querySelector(':focus').id === 'display') {
                    document.getElementById('display').checked = !document.getElementById('display').checked;
                    this.handleClick({target: document.getElementById('display')});
                }
            } else if (event.key === 'Escape' || (((navigator.platform === 'MacIntel' && event.metaKey) || (navigator.platform !== 'MacIntel' && event.ctrlKey)) && {'math': 'm', 'code': 'j', 'link': 'h', 'image': 'g'}[this.element] === event.key.toLowerCase())) {
                event.preventDefault();
                if (!document.getElementById('first').innerText.match(/\S+/g)) {
                    document.getElementById('edit').classList.remove('focus');
                    iframe.getElementById('editor').focus();
                } else {
                    let range = iframe.createRange();
                    let selection = document.getElementById('iframe').contentWindow.getSelection();
                    range.setStartAfter(iframe.querySelector('.selection'));
                    range.setEndAfter(iframe.querySelector('.selection'));
                    selection.removeAllRanges();
                    selection.addRange(range);
                    iframe.getElementById('editor').focus();
                    document.getElementById('edit').classList.remove('focus');
                    iframe.querySelector('.selection').classList.remove('selection');
                }
            } else if ((navigator.platform === 'MacIntel' && event.metaKey) || (navigator.platform !== 'MacIntel' && event.ctrlKey)) {
                if (('afxcbghjmrd'.includes(event.key.toLowerCase()) && event.shiftKey) || ('ibujghmrld'.includes(event.key.toLowerCase()) && !event.shiftKey)) {
                    event.preventDefault();
                }
            } else if (event.key === 'Tab') {
                if (event.target.id === 'display') {
                    event.preventDefault();
                    let selection = window.getSelection();
                    let range = document.createRange();
                    if (!event.shiftKey || document.getElementById('second').dataset.placeholder === 'undefined') {
                        range.setStartBefore(document.getElementById('first').lastChild);
                        selection.removeAllRanges();
                        selection.addRange(range);
                    } else {
                        range.setStartBefore(document.getElementById('second').lastChild);
                        selection.removeAllRanges();
                        selection.addRange(range);
                    }
                } else if (event.target.id === 'first') {
                    event.preventDefault();
                    if (event.shiftKey || document.getElementById('second').dataset.placeholder === 'undefined') {
                        document.getElementById('display').focus();
                    } else {
                        let selection = window.getSelection();
                        let range = document.createRange();
                        range.setStartBefore(document.getElementById('second').lastChild);
                        selection.removeAllRanges();
                        selection.addRange(range);
                    }
                }
            }
        }
    }

    delete(event) {
        let selection = document.getElementById('iframe').contentWindow.getSelection();
        let newRange = iframe.createRange();
        newRange.selectNode(iframe.querySelector('.selection'));
        selection.removeAllRanges();
        selection.addRange(newRange);
        iframe.execCommand('delete');
    }

    handleChange(event) {
        if (event.target.id === 'open') {
            let file = event.target.files[0];
            var reader = new FileReader();
            reader.onload = (event) => {
                iframe.getElementById('editor').focus();
                iframe.execCommand('selectAll');
                iframe.execCommand('insertHTML', false, event.target.result.match(/<div id="editor">(.*)<\/div>/s)[1]);
            }
            reader.readAsText(file);
        }
    }

    handleClick(event) {
        this.display = !this.display;
        if (event.target.id === 'display') {
            this.handleInput(event);
        } else {
            event.preventDefault();
            document.getElementById('display').checked = !document.getElementById('display').checked;
            this.handleInput();
            let selection = window.getSelection();
            let range = selection.getRangeAt(0);
            selection.removeAllRanges();
            selection.addRange(range);
        }
    }

    handleKeyUp(event) {
        event.preventDefault();
        if (event.code.match('Digit')) {
            document.getElementById(event.code.replace('Digit', '')).classList.remove('down');
        } else if (event.code.match('Key')) {
            document.querySelector('.row:not(:first-child) #' + event.code.replace('Key', '').toLowerCase()).classList.remove('down');
        } else if (Object.keys(this.keys).includes(event.code)) {
            document.getElementById(this.keys[event.code]).classList.remove('down');
        }

        if (!event.shiftKey) {
            if (document.getElementById('keyboard').classList.contains('shift')) {
                document.getElementById('keyboard').classList.remove('shift');
                if (document.querySelectorAll('.down')) {
                    Array.from(document.querySelectorAll('.down')).forEach((element) => {
                        element.classList.remove('down');
                    });
                }
            }
        }
        
        if (!((navigator.platform === 'MacIntel' && event.metaKey) || (navigator.platform !== 'MacIntel' && event.ctrlKey))) {
            if (document.getElementById('keyboard').classList.contains('command')) {
                document.getElementById('keyboard').classList.remove('command');
                if (document.querySelectorAll('.down')) {
                    Array.from(document.querySelectorAll('.down')).forEach((element) => {
                        element.classList.remove('down');
                    });
                }
            }
        }
    }

    render() {
        return (
            <div id="editor">
                <div id="keyboard">
                    {[
                        [['esc'], ['empty'], ['empty'], ['empty'], ['empty'], ['empty'], ['k', 'K'], ['e', 'E'], ['y', 'Y'], ['b', 'B'], ['o', 'O'], ['a', 'A'], ['r', 'R'], ['d', 'D']],
                        [['`', '~'], ['1', '!', 'heading 1'], ['2', '@', 'heading 2'], ['3', '#', 'heading 3'], ['4', '$'], ['5', '%'], ['6', '^'], ['7', '&'], ['8', '*'], ['9', '('], ['0', ')', 'paragraph'], ['-', '_'], ['=', '+'], ['delete']],
                        [['tab'], ['q', 'Q'], ['w', 'W'], ['e', 'E'], ['r', 'R'], ['t', 'T'], ['y', 'Y'], ['u', 'U'], ['i', 'I', 'italicize'], ['o', 'O', 'open', 'open'], ['p', 'P', 'print'], ['[', '{'] ,[']', '}'], ['\\', '|']],
                        [['caps lock'], ['a', 'A', 'select all'], ['s', 'S', 'save', 'download'], ['d', 'D'], ['f', 'F', 'find'], ['g', 'G', 'image'], ['h', 'H', 'link'], ['j', 'J', 'inline code', 'display code'], ['k', 'K', 'keyboard'], ['l', 'L', 'unordered list', 'ordered list'], [';', ':'], ['\'', '"'], ['return', 'return', 'page break']],
                        [['shift-left'], ['z', 'Z', 'undo', 'redo'], ['x', 'X', 'cut'], ['c', 'C', 'copy'], ['v', 'V', 'paste'], ['b', 'B', 'bold'], ['n', 'N'], ['m', 'M', 'inline math', 'display math'], [',', '<'], ['.', '>'], ['/', '?'], ['shift-right']],
                        [['control-left'], ['option-left'], ['command-left'], [' '], ['command-right'], ['option-right'], ['control-right'], ['left'], ['up'], ['down'], ['right']]
                    ].map((row, index) => 
                        <div key={index} className="row">{row.map((key, index) => 
                            <div key={index} className="key" id={key[0]} data-shift={key[1] || key[0]} data-command={key[2] || key[0]} data-both={key[3] || key[1] || key [0]}></div>
                        )}
                        </div>
                    )}
                    <input type="text"/>
                </div>
                <div id="edit">
                    <pre id="first" spellCheck="false" contentEditable="true" onInput={(event) => this.handleInput(event)} onDrop={(event) => this.handleDrop(event)}></pre>
                    <pre id="second" spellCheck="false" contentEditable="true" onInput={(event) => this.handleInput(event)}></pre>
                    <label id="label" onClick={(event) => this.handleClick(event)}>
                        <input type="checkbox" id="display"/>
                        <span/>
                        <div/>
                    </label>
                </div>
                <input id="open" type="file" onChange={(event) => this.handleChange(event)}/>
                <iframe id="iframe" title="iframe"></iframe>
            </div>
        )
    }
}

export default App;